.weixin{
    position: relative
}
.weixin img {
    position: absolute;
	z-index: 99;
	top: -170px;
	right: 38px;
	width: 120px;
	height: 120px;
	-webkit-transform: scale(0);
	        transform: scale(0);
	-webkit-transform-origin: top right;
	        transform-origin: top right;
	opacity: 0;
	/* border: .3125rem solid #0085ba; */
	border-radius: .25rem;
	-webkit-transition: all .4s ease-in-out;
	transition: all .4s ease-in-out;
}
.weixin:hover img {
	-webkit-transform: scale(1);
	        transform: scale(1);
	opacity: 1;
}

